import { Particle } from './particle.js';
import './style.css';

const COLOR = ['hsl(0,0%,100%)', "hsl(0,0%,80%)", "hsl(0,0%,60%)", "hsl(0,0%,40%)", "hsl(0,0%,20%)"]

class App{
    constructor(){
        this.canvas = document.createElement("canvas");
        this.ctx = this.canvas.getContext("2d");
        document.body.appendChild(this.canvas);

        this.pixel = window.devicePixelRatio > 1 ? 2 : 1;

        window.addEventListener("resize", this.resize.bind(this));
        this.resize();

        this.updatePositionX(this.stageWidth);
        this.create();


        window.requestAnimationFrame(this.draw.bind(this));
    }

    create(){
        this.particle = [];

        for (let i = 0; i < 1000; i++) {
            let curColor = Math.floor(Math.random() * 4);
            const item = new Particle(COLOR[curColor], this.positionX[i]);

            this.particle[i] = item;
        }
    }

    updatePositionX(stageWidth){
        this.positionX = [];
        for (let i = 0; i < 1000; i++) {
            const x = Math.random() * stageWidth;
            this.positionX[i] = x
        }
    }

    resize(){
        this.stageWidth = document.body.clientWidth;
        this.stageHeight = document.body.clientHeight;

        this.centalX = this.stageWidth / 2;
        this.centalY = this.stageHeight / 2;

        this.canvas.width = this.stageWidth * this.pixel;
        this.canvas.height = this.stageHeight * this.pixel;

        this.ctx.scale(this.pixel, this.pixel);
    }

    draw(){
        window.requestAnimationFrame(this.draw.bind(this));

        this.ctx.clearRect(0, 0, this.stageWidth, this.stageHeight);
        for (let i = 0; i < 1000; i++) {
            const rain = this.particle[i];
            rain.animate(this.ctx, this.stageHeight);
        }

        
    }
}

window.onload = () => {
    new App();
}